@import "../css/reset.css";

html{
    font-size:62.5%;
}

:root{
    --color-dark:black;
    --color-light-grey:#999;
    --color-grey:#555;
    --color-primary:#576B6B;
    --color-secondary:#53A0AC;
    --color-light:#FAF4E2;
    --color-pink:#FAC1C0;
}

*,
*::after,
*::before{
  box-sizing:border-box;
}

a{
  text-decoration: none;
}


body {
    line-height: 1.5;
    font-family:'Open Sans', Arial, Helvetica, sans-serif;
    color:var(--color-dark);  
    letter-spacing: 1px;
   
  }

h1,h2,h3{
    font-family: 'Pangolin', cursive, Arial, Helvetica, sans-serif; 
}

h1{
    font-size: 2.5rem;  
}
h2 {
  font-size: 2rem;  
}
h3{
  font-size: 1.6rem;  
}
h4{
  font-size: 1.6rem;  
}
h5{
  font-size: 1.6rem;
}
h6{
  font-size: 1.6rem;
}

p{
  font-size: 1.6rem; 
}



@media screen and (min-width:375px){
  h1{
    font-size: 3.6rem;
  }  
  h2 {
    font-size: 2.5rem;
  }  
  h3 {
    font-size: 2rem;
  } 
  h4{
    font-size: 2rem;  
  }
  h5{
    font-size: 1.6rem;
  }
  h6{
    font-size: 1.6rem;
  }
  p{
    font-size: 1.6rem; 
  }
}


@media screen and (min-width:768px){
    h1{
      font-size: 4rem;
    }  
    h2{
      font-size: 3rem;
    } 
    h3{
      font-size: 2.5rem;
    }  
    h4{
      font-size: 2.5rem;  
    }
    h5{
      font-size: 2.2rem;
    }
    h6{
      font-size: 2rem;
    }
    p{
      font-size: 2rem; 
    }
  }

@media screen and (min-width:1024px){
  h1{
    font-size: 6rem;
  }   
  
  h2{
    font-size: 3.6rem;
  } 
   h3{
    font-size: 3rem;
  } 
  h4{
    font-size: 3rem;  
  }
  h5{
    font-size: 2.8rem;
  }
  h6{
    font-size: 2.4rem;
  }
  p{
    font-size: 2.4rem; 
  }

}  

.dark{
  color:var(--color-dark);
}

.primary{
	color:var(--color-primary);
}
.secondary{
  color:var(--color-secondary);
}

.grey{
	color:var(--color-grey);
}

.light-grey{
  color: var(--color-light-grey);
}

.pink{
  color:var(--color-pink);
}

.white{
  color:white;
}

.italic{
  font-style: italic;
}

.underline{
  text-decoration: underline;
}

.header--padding{
  padding-top:1rem;
  padding-bottom:1rem;
}

.background-light{
  background-color: var(--color-light);
  border-radius: 3px;
}

.title-center{
  text-align: center;
}




.slide--heading{
    z-index:1;
    position:absolute;
    left:15%;
    top:10%;
    margin:3rem;
    color:var(--color-light);
    font-size:2.5rem;
} 

/* embla carousell */
@media screen and (min-width:768px){
    .slide--heading{
        left:50%;
    top:20%;
        margin:3rem;
        font-size:4rem;
    } 
  }

@media screen and (min-width:1024px){
    .slide--heading{
        left:40%;
    top:15%;
        margin:4rem;
        color:white;
        font-size:6rem;
    } 
}

/* nav */
.bars{
font-size:4rem;
color:var(--color-secondary);
}

.nav__brand>img{
  width:160px;
  transform:translateY(5px);
}

@media screen and (min-width:1024px){
  .bars{
    font-size:5rem;
    }
  .nav__brand>img{
    width:250px;
  }
}

.nav{
  display:flex;
  flex-direction: row;
  justify-content:space-between;
  flex-wrap:wrap;
  padding:1rem;
  align-items: center;

}

.nav__list{
  width:100%;
  margin:1rem;
}

.nav__item {
padding:0.5rem 2rem;
 width:100%;
}

.nav__item>a{
  color:var(--color-primary);
  transition: color 0.8s linear;
}

.nav__item>a:hover{
  color:var(--color-secondary);
}


.nav__toggler{

  transition:all 0.15s;
  cursor:pointer;
}

.nav.collapsible--expanded .nav__toggler{
 color:var(--color-primary);
}

.collapsible__content{
max-height:0;
overflow:hidden;
opacity:0;
transition:all 0.3s;

}

.collapsible--expanded .collapsible__content{
max-height:160vh;
opacity:1;  
}


/* about-us */
.about-us--collapsible .collapsible__content{

  max-height:160vh;
    opacity:1;
  }

  .about-us--collapsible .collapsible--expanded .collapsible__content{
    max-height:0;
    overflow:hidden;
    opacity:0;
    transition:all 0.3s;
  }
/* about-us end*/

.collapsible__header{
  display:flex;
  justify-content: space-between;
  position:relative;
}

.collapsible__chevron{
  transform:rotate(90deg);
  transition:transform 0.3s;
}

.collapsible--expanded .collapsible__chevron{
  transform:rotate(0);
}


@media screen and (min-width:768px){
  .nav__toggler{
    display:none;
  }

  .nav__list{
    display:flex;
    width:auto;
    font-size:2rem;
    max-height:100%;
    opacity:1;
  }

  .collapsible--expanded .nav__list{
    display:flex;
    width:auto;
  }
}

/* nav dropdown */
.nav--dropdown{
float:left;
overflow:hidden;
max-width:100%;
}

.nav--dropdown__content{
max-height:0;
overflow: hidden;
position: relative;
z-index:1;
-webkit-transition: max-height 0.5s ease-in-out;
    transition: max-height 0.5s ease-in-out;
 
color:var(--color-primary);
background-color: white;
margin-top: 1.5rem;
padding:0 1rem;
}

.nav--dropdown__content a{
 
  display: flex;
  flex-direction: column;
 padding-left:2rem;
 padding-bottom: 1rem;
 color:var(--color-secondary);
}

.nav--dropdown:hover .nav--dropdown__content{
  max-height:80vh;
}

.dropdown__content__list:hover{
  color:var(--color-primary);
}


@media screen and (min-width:768px){
.nav__item {
   width:auto;
  }

.nav--dropdown__content{
max-height:0;
position: absolute;
border-radius: 0 0 15px 15px;
-webkit-box-shadow: 0 10px 6px -6px #777;
     -moz-box-shadow: 0 10px 6px -6px #777;
          box-shadow: 0 10px 6px -6px #777;
}

.nav--dropdown__content a{
 padding-left:0;
 margin-top: 0.5rem;
}

.nav--dropdown:hover .nav--dropdown__content{
  border-top:3px solid var(--color-light);
}

}

hr {
    border-top: 3px solid #eee;
    margin: 3rem 0;
}

/* grid */
.grid{
	display:grid;
}

@media screen and (min-width:768px){
	.grid--1x2{
		grid-template-columns:repeat(2,1fr)
	}
}

@media screen and (min-width:1024px){
	.grid--1x3{
		grid-template-columns:repeat(3,1fr)
	}
}

/* button */

.btn{
	border-radius:20px;
    border:0;
	cursor:pointer;
	font-size:1.8rem;
	font-weight: 600;
	outline: 0;
	padding:0.5rem 2rem;
	text-transform: uppercase;
	text-align: center;
	margin: 1rem 0;
	transition: 0.3s;
}

.btn--light{
	background: var(--color-light);
}
.wrapper{
	max-width:1200px;
	margin:0 auto;
}

  /* card */
  .block{
  padding-top:1rem;
  padding-bottom:1rem;
  }

  .block-x{
    padding-right:2rem;
    padding-left:2rem;
    }

  @media screen and (min-width:768px){
	.block{
    padding-top:2rem;
    padding-bottom:2rem;
		}
    .block-x{
      padding-right:3rem;
      padding-left:3rem;
      }

	}
	@media screen and (min-width:1024px){
		.block{
			padding-top:3rem;
  padding-bottom:3rem;
			}
      .block-x{
        padding-right:4rem;
        padding-left:4rem;
        }
		}

  .card{
	  box-shadow: 0 0 20px 10px #f3f3f3;
	  border-radius:20px;
	  overflow:hidden;
	  margin:2rem;
  }

  .card--primary .card__header{
	  background:var(--color-primary);
	  color:#fff;
	  font-weight: 600;
  }

   .card--secondary .card__header{
	  background:var(--color-secondary);
	  color:#fff;
	  font-weight: 600;
  }

  .card__header,
  .card__body{
	  padding:2rem 3rem;
  }
  .card .btn{
	margin:2rem 3rem;
	
  }

  .card  .btn:hover{
	margin:2rem 3rem;
	background: var(--color-pink);
  }  

